          index: 5,
          label: 'H-EY0P - ZOO'
        }, {
          value: 967,
          index: 6,
          label: 'Otsasai VIII - Moon 3 - Caldari Navy Assembly Plant'
        }, {
          value: 953,
          index: 7,
          label: 'BKG-Q2 - Trit Star'
        }, {
          value: 947,
