      formatM3: function(val) {
        val = val.replace(/[^\d^\.]+/g,'');
        this.m3 = (val+ '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,');
        this.rewardCalculator();
      },
      formatCollateral: function(val) {
        val = val.replace(/[^\d^\.]+/g,'');
        this.collateral = (val+ '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,');
        this.rewardCalculator();
      },
      orgChanged: function(val) {
        for(var i = 0;i < this.options_dst.length;i++) {
          if (this.options_dst[i].value == val) {
            this.options_dst[i].disabled = true;
          } else {
            this.options_dst[i].disabled = false;
          }
        }
        if (this.shipTo == val) {
          this.shipTo = '';
        }
        for(var i = 0;i < this.options_org.length;i++) {
          if (this.options_org[i].value == val) {
            this.selected_org = this.options_org[i];
            break;
          }
        }
        this.rewardCalculator();
      },
      dstChanged: function(val) {
        for(var i = 0;i < this.options_org.length;i++) {
