            this.options_org[i].disabled = true;
          } else {
            this.options_org[i].disabled = false;
          }
        }
        if (this.shipFrom == val) {
          this.shipFrom = '';
        }
        for(var i = 0;i < this.options_dst.length;i++) {
          if (this.options_dst[i].value == val) {
            this.selected_dst = this.options_dst[i];
            break;
          }
        }
        this.rewardCalculator();
      },
      rewardCalculator: function() {
        if(this.shipFrom != '' && this.shipTo != '' && this.m3 != '' && this.collateral != '' && this.contractType != '') {
          // 体积运费变量
          var volumeReward = 0;
          // 保证金运费变量
          var collateralReward = 0;
          // 立方数代替变量
          var tmp_m3 = 150000;
          // 保证金代替变量
          var tmp_collateral = this.collateral.replace(/,/g,'');
          // 计算线路
          var d = (this.shipFrom * this.selected_org.index) - (this.shipTo - this.selected_dst.index);
          console.log(d);
          tmp_m3 = this.m3.replace(/,/g,'');
          // 判断体积是否超过最大限制
          if(tmp_m3 > 350000) {
            this.reward = '--';
            return;
          }

