          if (this.contractType == 'accelerated') {
            // 计算挡位
            if (tmp_m3 <= 150000) {
              tmp_m3 = 150000;
            } else if (tmp_m3 <= 250000) {
              tmp_m3 = 250000;
            } else {
              tmp_m3 = 350000;
            }
          }
