            // JITA -> RF-X7V
            case 1008:
              volumeReward = 1400 * tmp_m3;
              collateralReward =  tmp_collateral * 0.02;
              if (volumeReward < (tmp_collateral * 0.01)) {
                this.reward = collateralReward;
              } else {
                this.reward = volumeReward;
              }
              break;
            // RF-X7V -> JITA
            case 1986:
              volumeReward = 800 * tmp_m3 + (tmp_collateral * 0.02);
              this.reward = volumeReward;
              break;
            // Egbinger -> RF-X7V
            case 7587:
              volumeReward = 1200 * tmp_m3;
              collateralReward =  tmp_collateral * 0.02;
              if (volumeReward < (tmp_collateral * 0.01)) {
                this.reward = collateralReward;
              } else {
                this.reward = volumeReward;
              }
              break;
            // Podion -> RF-X7V
            case 8480:
              volumeReward = 1400 * tmp_m3;
              collateralReward =  tmp_collateral * 0.02;
              if (volumeReward < (tmp_collateral * 0.01)) {
