              }
              break;
            // JITA -> 0R-F2F
            case 1024:
              volumeReward = 700 * tmp_m3;
              collateralReward =  tmp_collateral * 0.02;
              if (volumeReward < (tmp_collateral * 0.01)) {
                this.reward = collateralReward;
              } else {
                this.reward = volumeReward;
              }
              break;
            // 0R-F2F -> JITA
            case 3912:
              volumeReward = 500 * tmp_m3 + (tmp_collateral * 0.02);
              this.reward = volumeReward;
              break;
            // JITA -> H-EY0P
